import request from '@/apis/request'

// 获取机构列表
export function getDeptApi(data) {
  return request({
    url: '/sys/office/list',
    method: 'post',
    data
  })
}

// 新增机构列表
export function addDeptApi(data) {
  return request({
    url: '/sys/office/add',
    method: 'post',
    data
  })
}

// 修改机构列表
export function editDeptApi(data) {
  return request({
    url: '/sys/office/edit',
    method: 'post',
    data
  })
}

// 删除机构列表
export function delDeptApi(data) {
  return request({
    url: '/sys/office/delete',
    method: 'post',
    data
  })
}

// 根据父节点id查询子节点
export function getListByPidApi(data) {
  return request({
    url: '/sys/office/childernList',
    method: 'post',
    data
  })
}

// 查询完整机构列表，没有缓存
export function getDepsNoCacheApi(data) {
  return request({
    url: '/sys/office/listTreeView',
    method: 'post',
    data
  })
}
