import { mapGetters } from 'vuex'
import OriginUserTree from '@/components/OriginUserTree'
import TableColumnChoose from '@/components/TableColumnChoose'
import { Notification } from 'element-ui'
import { getListApi as MaterialGroupApi } from '@/apis/modules/base/baseItemGroup'

export const TABLE_TYPE_MAP = [
  {
    title: '物料组',
    type: 'MATERIAL_GROUP',
    thead: [
      { label: '物料组编号', prop: 'code' },
      { label: '物料组描述', prop: 'description' },
      { label: '数据来源', prop: 'dataSources_dictText' }
    ],
    apiFn: MaterialGroupApi
  }
]

// 单页面多表单选择
export const singlePageMultipleTableColumnChooseMixins = {
  components: {
    TableColumnChoose
  },
  data() {
    return {
      // 选择
      tableColumnChooseLoading: false,
      tableColumnChooseVisable: false,
      tableColumnChooseQuery: {},
      tableColumnChooseData: {},
      chooseFormThead: [],
      tableColumnChooseTitle: '',
      tableColumnChooseType: '',
      materielTypeOpts: [],
      tableColumnChooseExtraHandle: {},
      tableColumnChooseOrder: []
    }
  },
  methods: {
    // 初始化表格选择器
    initTableColumnChoose(page = { current: 1, size: 10 }) {
      // 请求列表参数
      let params = {
        page: page.size ? page : this.pageInfo,
        ...this.tableColumnChooseQuery /** 筛选表单 */
      }
      // 获取请求接口
      const choose = TABLE_TYPE_MAP.filter(item => item.type === this.tableColumnChooseType)[0]
      const { apiFn } = choose

      return new Promise((resolve, reject) => {
        /**
         * 特殊 请求体整理
         */
        // 1. bom
        if (this.tableColumnChooseType === 'PRODUCT_BOM') { // bom
          params = { ...this.tableQuery }
        }

        // 有选项顺序的选择的拦截和添加参数
        const intercept = this.extraTableColumnQueryAdd()
        if (intercept.isAddParams) {
          const { addParams } = intercept
          params = Object.assign(params, addParams)
          const delList = this.initOrderQueryListMixins()
          // 删除此选项后面所有的form
          delList.forEach(item => {
            this.$set(this.form, item.formKey, null)
          })
          console.log('选择列表的查询参数=======>', params)
        }
        // 修改完参数复制给 tableColumnChooseQuery
        const q = JSON.parse(JSON.stringify(params))
        q.page ? delete q.page : null
        this.tableColumnChooseQuery = q
        // 请求弹出列表
        apiFn(params).then(res => {
          const { code, data } = res
          if (!code) {
            if (Array.isArray(data)) {
              this.tableColumnChooseData = { list: data }
            } else {
              this.tableColumnChooseData = data
            }
          }
          resolve(res)
          this.tableColumnChooseLoading = false
        }).catch(err => {
          reject(err)
          this.tableColumnChooseLoading = false
        })
      })
    },
    /**
     * 有选项的删除当前选项后面的
     * @returns
     */
    initOrderQueryListMixins() {
      const o = this.tableColumnChooseOrder
      const index = o.findIndex(item => item.type === this.tableColumnChooseType)
      return o.slice(index + 1)
    },
    /**
     * 有选项顺序的拦截和参数的整理
     * @returns {isAddParams}  Boolean 是否是有顺序的弹出框
     * @returns {addParams}  Object 查询弹出列表时候需要添加的查询参数
     * @returns {errMsg}  String 不符合顺序的时候提示的错误信息
     */
    extraTableColumnQueryAdd() {
      const queryOrder = this.tableColumnChooseOrder
      const queryAdd = queryOrder.some(item => item.type === this.tableColumnChooseType)
      const isAddParams = Array.isArray(queryOrder) && queryOrder.length && queryAdd
      let result = { isAddParams }

      if (isAddParams) {
        const { preFormKey, err, query } = queryOrder.filter(item => item.type === this.tableColumnChooseType)[0]
        const errMsg = this.form[preFormKey] ? '' : err
        result = Object.assign(result, { preFormKey, err, query, errMsg, addParams: { [query]: this.form[preFormKey] }})
      }
      return result
    },
    // 弹出表格选择器
    handleShowTableColumnChoose(type) {
      this.tableColumnChooseType = type
      const choose = TABLE_TYPE_MAP.filter(item => item.type === type)[0]
      const { thead, title } = choose
      // 设置标题跟表头数据
      this.tableColumnChooseTitle = title
      this.chooseFormThead = thead
      // 重置搜索项
      this.tableColumnChooseQuery = {}
      // 有顺序选项的拦截和提示
      const intercept = this.extraTableColumnQueryAdd()
      if (intercept.isAddParams && intercept.errMsg !== '') {
        Notification({
          title: intercept.errMsg,
          type: 'warning',
          duration: 2 * 1000
        })
        return false
      }
      // 打开弹窗
      this.tableColumnChooseVisable = true
      // 开启loading
      this.tableColumnChooseLoading = true
      this.initTableColumnChoose()
    },
    resetTableColumnChooseForm(formName) {
      this.$refs[formName].resetFields()
      this.initTableColumnChoose()
    },
    getNowMDays(value) {
      if (value) {
        return value
      }
      const date = new Date()
      let year = date.getFullYear()
      let month = date.getMonth() + 2
      if (month > 12) {
        year = year + 1
        month = month - 12
      }
      return `${year}-${month < 10 ? `0${month}` : month}`
    },
    rowClickTableChooseColumn() {}
  }
}

// 页面多个多选框mixins
export const dynamicTagsMultipleTableColumnChooseMixins = {
  data() {
    return {
      tableColumnChooseExtraHandle: {
        handleRowClick: 'rowClickTableChooseColumn'
      },
      dynamicTagsMultipleChooseList: [],
      tableColumnMultiple: false, // 是否多选
      extraList: [], // 多选时显示内容
      dynamicTags: [] // 中介内容，临时存储一些内容
    }
  },
  methods: {
    getProp() {
      return ''
    },
    rowClickTableChooseColumnExitTask(item) {
      return item
    },
    dynamicTagsMultipleApiList() {
      const result = {}
      this.dynamicTagsMultipleChooseList.forEach(element => {
        const { value, api } = element
        const list = this.form[value] || []
        result[api] = list.map(
          (item) => item.id
        )
      })
      return result
    },
    rowClickTableChooseColumn(item) {
      if (!this.tableColumnMultiple) {
        // 单条数据
        return
      }
      if (!this.dynamicTags.some((i) => i.id === item.id)) {
        item = this.rowClickTableChooseColumnExitTask(item)
        const list = [...this.dynamicTags, { ...item }]
        this.dynamicTags = list // 当前选定内容
        this.extraList = [...list] // 当前选定内容，用于显示
      } else {
        this.handleRemoveChooseTag(item)
      }
    },
    showTableColumnChooseExitTask() {},
    /**
     * 显示选择列表
     * @param {String} type 列表的类型
     * @param {Boolean} multiple  是否多选
     */
    showTableColumnChoose(type, multiple) {
      this.showTableColumnChooseExitTask(type)
      this.handleShowTableColumnChoose(type)
      this.tableColumnMultiple = multiple
      const prop = this.getProp() // 确定当前选择类型
      this.extraList = this.form[prop] || [] // 回显数据
      this.dynamicTags = this.form[prop] || []// 临时存储当前数据
    },
    // 选择内容 基本内容 （单项内容） 请添加额外处理内容函数 chooseColumnExitTask()
    chooseColumn(item) {
      if (this.tableColumnMultiple) {
        const prop = this.getProp()
        this.form[prop] = [...this.dynamicTags]
      }
      // 以下内容有单条选择
      this.chooseColumnExitTask(item)

      this.resetTableColumnChooseForm('tableColumnChooseFilter')
      this.tableColumnChooseVisable = false
      this.extraList = []
      this.dynamicTags = []
    },
    // 删除选择的tag
    handleRemoveChooseTag(tag, type) {
    // 从form中删除时有type值
      if (type) {
        this.tableColumnChooseType = type
        const prop = this.getProp()
        const list = this.form[prop].filter((item) => item.id !== tag.id)
        this.form[prop] = [...list]
        return
      }
      // 选择对话框
      const list = this.dynamicTags.filter((item) => item.id !== tag.id)
      this.extraList = [...list]
      this.dynamicTags = [...list]
    },
    tableInput() {
      this.extraList = []
      this.dynamicTags = []
      this.tableColumnChooseQuery = {}
    }
  }
}

// 用户单选
export const originUserChooseMixins = {
  components: {
    OriginUserTree
  },
  data() {
    return {
      // 人员选择
      userDialogVisible: false
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'userInfo'
    })
  }
}
